import { FC } from 'react';
import ContentLoader from 'react-content-loader';

export const LoaderLeft: FC = (props) => (
  <ContentLoader
    backgroundColor="#f3f3f3"
    foregroundColor="#ecebeb"
    height={331}
    speed={2}
    viewBox="0 0 357 331"
    width={357}
    {...props}
  >
    <rect height="301" rx="16" ry="16" width="357" x="0" y="0" />
  </ContentLoader>
);
